@import '../../styles/sass/variables.scss';

.social {
    border: 1px solid;
    width: 280px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0;
    margin: 6px;
    list-style: none;
    .icon {
        color: $primeryColor;
        &:hover {
            transform: scale(1.05);
            transition: 0.2s;
        }
    }
    @media screen and (max-width: 420px) {
        display: none;
    }

}